import React from 'react';
import { Link } from 'react-router-dom';
const Header = () => {
  return (
    <header style={headerStyle}>
      <h1>TODO-LIST!</h1>
      <Link to="/">Home</Link> | <Link to="/about">About</Link>
    </header>
  );
};

export default Header;
const headerStyle = {
  background: '#333',
  color: '#fff',
  padding: '1rem',
  textAlign: 'center',
};
