import React, { Component } from 'react';

export default class AddTodo extends Component {
  state = {
    title: '',
  };

  onChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  onSubmit = (e) => {
    e.preventDefault();
    this.setState({ title: '' });
    this.props.addTodo(this.state.title);
  };

  render() {
    return (
      <form
        onSubmit={this.onSubmit}
        style={{
          display: 'flex',
          padding: '1rem',
          //   margin: '1rem',
        }}
      >
        <input
          style={{ flex: '10', outline: 'none' }}
          type="text"
          name="title"
          placeholder="Add Todo"
          value={this.state.title}
          onChange={this.onChange}
        />
        <input
          style={{ flex: '1' }}
          type="submit"
          value="Submit"
          className="btn"
        />
      </form>
    );
  }
}

