import React from 'react';

const About = () => {
  return (
    <React.Fragment>
      <div className="about">
        <h1 style={hstyle}>About</h1>
        <p style={pstyle}>
          THIS IS MY FIRST REACT APP! THANK YOU TO{' '}
          <a
            style={linkStyle}
            href="https://www.youtube.com/user/TechGuyWeb"
            target="_blank"
          >
            Traversy Media
          </a>{' '}
          FOR TEACHING THIS!
        </p>
      </div>
    </React.Fragment>
  );
};

export default About;

const linkStyle = {
  color: 'cornflowerblue',
  textDecoration: 'underline',
};

const pstyle = {
  margin: '1rem',
  textAlign: 'center',
};

const hstyle = {
  textAlign: 'center',
};
